/* font */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap");

@import "./_reset.scss";
@import "./_variable.scss";

/* components */
@import "./components/_header.scss";
@import "./components/_arrow-up.scss";
@import "./components/_animation.scss";
@import "./components/_status-bar.scss";

/* screen */
@import "./screen/_prev.scss";
@import "./screen/_home.scss";
@import "./screen/_project.scss";
@import "./screen/_about-me.scss";
@import "./screen/_contact.scss";

body {
  font-family: "Poppins", sans-serif;
  width: 100%;
  overflow-x: hidden;
  /* contain: style layout inline-size; */
  background-color: gray;
  position: relative;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

body::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera*/
}

/* Typography */
h1 {
  font-size: var(--font-large);
  font-weight: var(--weight-bold);
  color: var(--color-black);
  margin: 16px 0px;
}

h2 {
  font-size: var(--font-medium);
  font-weight: var(--weight-semi-bold);
  color: var(--color-white);
  margin: 8px 0;
}

h3 {
  font-size: var(--font-regular);
  font-weight: var(--font-regular);
  color: var(--color-black);
  margin: 8px 0;
}

p {
  font-size: var(--font-regular);
  font-weight: var(--font-regular);
  color: var(--color-black);
  margin: 4px 0;
}

/* Section common */
.section {
  /* padding: 0 50px; */
  text-align: center;
  margin: auto;
}

.section__container {
  max-width: 1200px;
  margin: auto;
}

.swiper-slide:nth-child(1) .slide-img {
  background-image: url("../../public/img/pc-TDS-order-develop.png");
}
.swiper-slide:nth-child(2) .slide-img {
  background-image: url("../../public/img/pc-TW-myshop.png");
  object-fit: cover;
}
.swiper-slide:nth-child(3) .slide-img {
  background-image: url("../../public/img/pc-TDS-seller-develop.png");
}
.swiper-slide:nth-child(4) .slide-img {
  background-image: url("../../public/img/pc-TDS-eSIM-tab.png");
}
.swiper-slide:nth-child(5) .slide-img {
  background-image: url("../../public/img/pc-seashore.png");
}
.swiper-slide:nth-child(6) .slide-img {
  background-image: url("../../public/img/pc-iphone.png");
}

/* For below 768px screen width */
@media screen and (max-width: 768px) {
  .section {
    padding: 16px;
    padding-top: 40px;
  }
}
